___CSS_LOADER_EXPORT___.push([module.id, ".max-container{height:500px;overflow-y:auto}", "",{"version":3,"sources":["webpack://./resources/js/components/PurchaseSmsNumber.vue"],"names":[],"mappings":"AAoSA,eAEE,YAAa,CADb,eAEF","sourcesContent":["<style type=\"text/css\">\n#purchaseBtn:disabled {\n  cursor: not-allowed;\n  opacity: 0.8;\n}\n</style>\n<template>\n  <div>\n    <div class=\"flex-shrink-0 ml-auto pr-2\">\n      <button\n        @click=\"show = true\"\n        class=\"flex-shrink-0 shadow rounded focus:outline-none ring-primary-200 dark:ring-gray-600 focus:ring bg-primary-500 hover:bg-primary-400 active:bg-primary-600 text-white dark:text-gray-800 inline-flex items-center font-bold px-4 h-9 text-sm flex-shrink-0\"\n      >\n        Purchase Sms Number\n      </button>\n    </div>\n    <Modal\n      :show=\"show\"\n      data-testid=\"confirm-upload-removal-modal\"\n      role=\"alertdialog\"\n    >\n      <form\n        @submit.prevent=\"submit\"\n        class=\"bg-white dark:bg-gray-800 rounded-lg shadow-lg overflow-hidden\"\n        style=\"width: 460px\"\n      >\n        <div id=\"search_number\" v-show=\"showSearchFields\">\n          <ModalHeader v-text=\"__('Purchase Sms Number')\" />\n          <ModalContent>\n            <div class=\"flex relative w-full mb-2\">\n              <select\n                name=\"filter\"\n                id=\"filter\"\n                @change=\"setFilter\"\n                class=\"w-full block form-control form-select form-select-bordered\"\n                v-model=\"data.filter\"\n              >\n                <option value=\"empty\" selected>Select Filter</option>\n                <option value=\"contains\">Contains</option>\n                <option value=\"starts_with\">Starts With</option>\n                <option value=\"ends_with\">Ends With</option>\n              </select>\n            </div>\n            <div class=\"flex relative w-full mb-2\">\n              <input\n                class=\"w-full block form-control form-input form-input-bordered\"\n                name=\"value\"\n                placeholder=\"Value\"\n                v-model=\"data.value\"\n                required\n                v-if=\"filter === 'contains' || filter === 'starts_with' || filter === 'ends_with'\"\n              />\n            </div>\n\n          </ModalContent>\n          <ModalFooter>\n            <div class=\"ml-auto\">\n              <LinkButton\n                dusk=\"cancel-upload-delete-button\"\n                type=\"button\"\n                @click.prevent=\"\n                  ;(show = false),\n                    (showSearchTable = false)\n                \"\n                class=\"mr-3\"\n              >\n                {{ __('Cancel') }}\n              </LinkButton>\n              <LoadingButton\n                type=\"submit\"\n                class=\"flex-shrink-0 shadow rounded focus:outline-none ring-primary-200 dark:ring-gray-600 focus:ring bg-primary-500 hover:bg-primary-400 active:bg-primary-600 text-white dark:text-gray-800 inline-flex items-center font-bold px-4 h-9 text-sm flex-shrink-0\"\n                v-show=\"showSearchFields\"\n                :loading=\"loading\"\n                @click=\"\n                  loading = true\n                \"\n              >\n                Search\n              </LoadingButton>\n            </div>\n          </ModalFooter>\n        </div>\n        <div id=\"search_table\" v-show=\"showSearchTable\">\n          <ModalHeader v-text=\"__('Purchase Sms Number')\" />\n          <ModalContent class=\"max-container\">\n            <table\n              class=\"w-full\"\n              cellpadding=\"2\"\n              cellspacing=\"2\"\n              data-testid=\"resource-table\"\n            >\n              <thead>\n                <tr class=\"userDatatable-header\">\n                  <th>\n                    <span class=\"userDatatable-title\">#</span>\n                  </th>\n                  <th>\n                    <span class=\"userDatatable-title\">Number</span>\n                  </th>\n                  <!--\n                  <th>\n                    <span class=\"userDatatable-title\">Npa</span>\n                  </th>\n                  <th>\n                    <span class=\"userDatatable-title\">Nxx</span>\n                  </th>\n                  <th>\n                    <span class=\"userDatatable-title\">Xxxx</span>\n                  </th>\n                  -->\n                  <!-- <th v-if=\"type === 'local'\">\n                    <span class=\"userDatatable-title\">State</span>\n                  </th>\n                  <th v-if=\"type === 'local'\">\n                    <span class=\"userDatatable-title\">Ratecenter</span>\n                  </th> -->\n                </tr>\n              </thead>\n              <tbody>\n                <template v-for=\"(value, key) in searchNumbers\">\n                  <tr class=\"text-center\">\n\n                    <td>\n                      <input\n                        type=\"checkbox\"\n                        name=\"check_row\"\n                        :value=\"value.e164\"\n                        v-model=\"selectedFields\"\n                      />\n                    </td>\n                    <td>\n                      {{\n                        maskIt(\n                          value.e164\n                        )\n                      }}\n                    </td>\n                    <!--<td>{{ value.npa }}</td>\n                    <td>{{ value.nxx }}</td>\n                    <td>{{ value.xxxx }}</td>-->\n                    <!-- <td v-if=\"type === 'local'\">{{ value.state }}</td>\n                    <td v-if=\"type === 'local'\">{{ value.ratecenter }}</td> -->\n                  </tr>\n                </template>\n              </tbody>\n            </table>\n          </ModalContent>\n          <ModalFooter>\n            <div class=\"ml-auto\">\n              <LinkButton\n                dusk=\"cancel-upload-delete-button\"\n                type=\"button\"\n                @click.prevent=\"\n                  ;(show = false),\n                    (showSearchTable = false),\n                    (showSearchFields = true)\n                \"\n                class=\"mr-3\"\n              >\n                {{ __('Cancel') }}\n              </LinkButton>\n              <LoadingButton\n                type=\"button\"\n                class=\"flex-shrink-0 shadow rounded focus:outline-none ring-primary-200 dark:ring-gray-600 focus:ring bg-primary-500 hover:bg-primary-400 active:bg-primary-600 text-white dark:text-gray-800 inline-flex items-center font-bold px-4 h-9 text-sm flex-shrink-0\"\n                v-show=\"showSearchTable\"\n                @click.stop=\"purchaseNumber\"\n                id=\"purchaseBtn\"\n                :loading=\"loading\"\n                :disabled=\"\n                  disabled == true ||\n                  (selectedFields.length === 0 ? true : false)\n                \"\n              >\n                Purchase\n              </LoadingButton>\n            </div>\n          </ModalFooter>\n        </div>\n      </form>\n    </Modal>\n  </div>\n</template>\n<script>\nimport { mask } from 'maska'\nexport default {\n  // directives: { mask },\n  data() {\n    return {\n      show: false,\n      loading: false,\n      states: '',\n      ratecenter: '',\n      type: '',\n      filter: '',\n      showSearchFields: true,\n      showSearchTable: false,\n      data: {\n        filter: '',\n        value: '',\n      },\n      selectedFields: [],\n      searchNumbers: '',\n      disabled: false,\n    }\n  },\n  async created() {\n    this.loading = false\n    // console.log('asdasd')\n    //   get states\n    // await Nova.request()\n    //   .post('/nova-api/numbers/get-states')\n    //   .then(response => {\n    //     if (response.data.error === null) {\n    //       let data = response.data.data\n    //       this.states = data\n    //     }\n    //   })\n  },\n  methods: {\n    maskIt(i) {\n      return mask(i, '+1 (###) ###-####')\n    },\n    setFilter(e) {\n      if (e.target.value !== '') this.filter = e.target.value\n      console.log(this.filter)\n    },\n    async submit() {\n      // this.showSearchFields = false\n      // this.showSearchTable = true\n      this.loading = true\n      let params = {\n        filter: this.data.filter,\n        value: this.data.value,\n      }\n\n      await Nova.request()\n        .post('/nova-api/numbers/get-sms-numbers', params)\n        .then(response => {\n          this.showSearchFields = false\n          this.showSearchTable = true\n          this.loading = false\n          let numbers = response.data.data.data\n\n          if (numbers.length > 0) {\n            this.searchNumbers = numbers\n          }\n\n          console.log(searchNumbers)\n        })\n    },\n\n    async purchaseNumber() {\n      this.loading = true\n      this.disabled = true\n      let numbers = this.selectedFields\n    //   let name = this.data.name\n    //   let filter = this.data.filter\n    //   let value = this.data.value\n    //   let description = this.data.description\n      let params = {\n        numbers: numbers,\n        // name: name,\n        // description: description,\n      }\n\n      await Nova.request()\n        .post('/nova-api/numbers/purchase-sms-numbers', params)\n        .then(response => {\n          this.loading = false\n          this.disabled = false\n          this.showSearchFields = false\n          this.showSearchTable = false\n          this.show = false\n\n          if (response.data.success) {\n            Nova.success('Purchase Number successfully')\n          } else {\n            Nova.error(response.data.error)\n          }\n\n        //   if (response.data.role === 'user') {\n        //     Nova.visit('/resources/my-numbers', { remote: false })\n        //   } else {\n            Nova.visit('/resources/sms-numbers', { remote: false })\n        //   }\n        })\n    },\n  },\n}\n</script>\n\n<style>\n.max-container {\n  overflow-y: auto;\n  height: 500px;\n}\n</style>\n"],"sourceRoot":""}]);
