___CSS_LOADER_EXPORT___.push([module.id, ".fieldmapper table{margin-top:10px;width:100%}", "",{"version":3,"sources":["webpack://./resources/js/components/UploadCallerIdContactList.vue"],"names":[],"mappings":"AA+WA,mBAEE,eAAgB,CADhB,UAEF","sourcesContent":["<template>\n  <div>\n    <div class=\"flex-shrink-0 ml-auto pr-2\">\n      <button\n        @click=\"show = true\"\n        class=\"\n          flex-shrink-0\n          shadow\n          rounded\n          focus:outline-none\n          ring-primary-200\n          dark:ring-gray-600\n          focus:ring\n          bg-primary-500\n          hover:bg-primary-400\n          active:bg-primary-600\n          text-white\n          dark:text-gray-800\n          inline-flex\n          items-center\n          font-bold\n          px-4\n          h-9\n          text-sm\n          flex-shrink-0\n        \"\n      >\n        Add Contact List\n      </button>\n    </div>\n    <Modal\n      :show=\"show\"\n      data-testid=\"confirm-upload-removal-modal\"\n      role=\"alertdialog\"\n    >\n      <div\n        class=\"bg-white dark:bg-gray-800 rounded-lg shadow-lg overflow-hidden\"\n        style=\"width: 460px\"\n      >\n        <ModalHeader v-text=\"__('Upload Contact List')\" />\n        <ModalContent>\n          <div class=\"flex relative w-full mb-2\" v-if=\"isSuper\">\n            <div class=\"md:w-1/5\">\n              <label for=\"\">Company:</label>\n            </div>\n            <div class=\"flex-1\">\n              <select\n                v-if=\"isSuper\"\n                name=\"company\"\n                id=\"company\"\n                class=\"\n                  w-full\n                  block\n                  form-control form-select form-select-bordered\n                \"\n                v-model=\"company_id\"\n                required\n              >\n                <option\n                  v-for=\"company in companies\"\n                  :key=\"company.id\"\n                  :value=\"company.id\"\n                >\n                  {{ company.name }}\n                </option>\n              </select>\n            </div>\n          </div>\n\n          <div class=\"flex relative w-full mb-2\">\n            <div class=\"md:w-1/5\">\n              <label for=\"\">Type:</label>\n            </div>\n            <div class=\"flex-1\">\n              <select\n                name=\"type\"\n                id=\"type\"\n                class=\"\n                  w-full\n                  block\n                  form-control form-select form-select-bordered\n                \"\n                v-model=\"type\"\n                required\n              >\n                <option value=\"csv\">CSV</option>\n                <option value=\"individual\">INDIVIDUAL</option>\n              </select>\n            </div>\n          </div>\n\n          <div class=\"flex relative w-full mb-2\" v-if=\"type == 'individual'\">\n            <div class=\"md:w-1/5\">\n              <label for=\"\">Number:</label>\n            </div>\n            <div class=\"flex-1\">\n              <input\n                type=\"text\"\n                class=\"bg-white border focus:outline-none p-3 w-full\"\n                placeholder=\"+1 (123) 456-7890\"\n                v-maska=\"'+1 (###) ###-####'\"\n                v-model=\"number\"\n                required\n              />\n            </div>\n          </div>\n          <br />\n          <div class=\"fieldmapper\">\n            <vue-csv-import\n              v-if=\"type == 'csv'\"\n              v-model=\"csv\"\n              :fields=\"{ phone: { required: true, label: 'Phone' } }\"\n            >\n              <!-- <vue-csv-toggle-headers></vue-csv-toggle-headers> -->\n              <vue-csv-errors> </vue-csv-errors>\n              <vue-csv-input\n                name=\"file\"\n                v-model=\"file\"\n                :fileMimeTypes=\"['text/csv', 'text/x-csv']\"\n                :headers=\"true\"\n              ></vue-csv-input>\n              <vue-csv-map :auto-match=\"true\"></vue-csv-map>\n            </vue-csv-import>\n          </div>\n        </ModalContent>\n        <ModalFooter>\n          <div class=\"ml-auto\">\n            <DangerButton\n              dusk=\"cancel-upload-delete-button\"\n              type=\"button\"\n              @click.prevent=\"show = false\"\n              class=\"mr-3\"\n            >\n              {{ __('Cancel') }}\n            </DangerButton>\n\n            <LoadingButton\n              :disabled=\"saveContactListButtonDisable\"\n              :loading=\"saveContactListButtonDisable\"\n              class=\"\n                flex-shrink-0\n                shadow\n                rounded\n                focus:outline-none\n                ring-primary-200\n                dark:ring-gray-600\n                focus:ring\n                bg-primary-500\n                hover:bg-primary-400\n                active:bg-primary-600\n                text-white\n                dark:text-gray-800\n                inline-flex\n                items-center\n                font-bold\n                px-4\n                h-9\n                text-sm\n                flex-shrink-0\n              \"\n              @click.prevent=\"saveContactList\"\n            >\n              <!-- <vue-csv-submit url=\"/post/here\"></vue-csv-submit> -->\n              Create\n            </LoadingButton>\n          </div>\n        </ModalFooter>\n      </div>\n    </Modal>\n  </div>\n</template>\n\n<script>\nimport {\n  VueCsvToggleHeaders,\n  VueCsvSubmit,\n  VueCsvMap,\n  VueCsvInput,\n  VueCsvErrors,\n  VueCsvImport,\n} from 'vue-csv-import'\nimport { maska } from 'maska'\nimport { mapGetters, mapMutations } from 'vuex'\n\nexport default {\n  components: {\n    VueCsvImport,\n    VueCsvErrors,\n    VueCsvInput,\n    VueCsvMap,\n    VueCsvSubmit,\n    VueCsvToggleHeaders,\n  },\n  directives: {\n    maska,\n  },\n  props: {\n    responseLink: {\n      default: '/resources/caller-id-contact-lists',\n      type: String,\n      required: false,\n    },\n  },\n  data() {\n    return {\n    chunkSize:1000,\n      company_id: null,\n      companies: [],\n      file: null,\n      errors: [],\n      type: 'csv', //csv,individual\n      show: false,\n      csv: null,\n      number: '',\n      isSuper: false,\n      saveContactListButtonDisable:false,\n    }\n  },\n  watch: {\n\n  },\n  computed: {\n    ...mapGetters(['currentUser']),\n  },\n  mounted() {\n    this.$nextTick(()=>{\n\n        this.company_id = this.currentUser.company_id\n        let role = this.currentUser.role\n        if (role === 'admin') {\n        this.isSuper = true\n        Nova.request()\n            .post('/nova-api/caller-id-contact-lists/getcompanies', {})\n            .then(res => {\n            console.log('res', res.data)\n            if (res.data.companies) {\n                this.companies = res.data.companies\n            }\n            })\n        }\n\n    });\n  },\n  methods: {\n    showFields() {\n    //   console.log('csv', this.csv)\n    },\n    chunks:function*(arr, n) {\n        for (let i = 0; i < arr.length; i += n) {\n            yield arr.slice(i, i + n);\n        }\n    },\n    saveContactList() {\n      if (!this.company_id) {\n        Nova.warning('Empty or invalid Records')\n        return\n      }\n\n        if (this.number && this.type==\"individual\"){\n            this.saveContactListButtonDisable = true;\n\n            Nova.request()\n            .post('/nova-api/caller-id-contact-lists/upload', {\n                type: this.type,\n                number: this.number,\n                company_id: this.company_id,\n                file: [],\n            }).then(res => {\n                console.log('res', res.data)\n                if (res.data.status == 'success') {\n                    this.saveContactListButtonDisable = false\n                    this.show = false\n                    Nova.info(res.data.message);\n\n                    if (this.responseLink){\n                        Nova.visit(this.responseLink, { remote: false })\n                    }\n                }else{\n                    this.saveContactListButtonDisable = false;\n                    Nova.error(res.data.message);\n                }\n            }).catch(error => {\n                Nova.error('An error came')\n                console.log('error', error)\n                this.saveContactListButtonDisable = false\n            });\n        } else if (\n            this.csv &&\n            this.csv.length &&\n            this.type == \"csv\"\n            ){\n\n            console.log(\"this.csv:\",this.csv);\n            this.saveContactListButtonDisable = true;\n\n            let chunksBuffer=[...this.chunks(this.csv, this.chunkSize)];\n            console.log(\"chunksBuffer:\",chunksBuffer);\n\n            let totals=this.csv.length;\n            let chunksTotals=chunksBuffer.length;\n            let uniqueKey='unique-'+Date.now().toString(32)+'-'+(Math.random(999)*10000);\n            let errorBucket=[];\n            chunksBuffer.forEach((csv,i) => {\n                csv=csv.map((c)=>c.phone);\n                Nova.request()\n                .post('/nova-api/caller-id-contact-lists/upload', {\n                    type: this.type,\n                    number: '',\n                    company_id: this.company_id,\n                    file: csv,\n\n                    meta:{\n                        chunksize:this.chunkSize,\n                        chunk_total:chunksTotals,\n                        index:i,\n                        totals:totals,\n                        unique:uniqueKey\n                    }\n                }).then(res => {\n                    console.log('res', res.data)\n                    if (res.data.status == 'success') {\n\n\n                        if(chunksTotals ==  (1 + res.data.meta.index)){\n                            // this.saveContactListButtonDisable = false\n                            this.show = false\n                                Nova.info(res.data.message);\n\n                            if (this.responseLink){\n                                Nova.visit(this.responseLink, { remote: false });\n                            }\n                        }\n                    }else{\n                        // this.saveContactListButtonDisable = false\n                        errorBucket.push(res.data.message);\n                        if(chunksTotals == (1+res.data.meta.index)){\n                            this.saveContactListButtonDisable = false\n                            //here display all error responces from bucket\n                            let errorsText = errorBucket.filter(e=>!!e.length).join('<br>');\n                            Nova.error(errorsText);\n                            console.log('error:', errorBucket, ': At index:',res.data.meta.index);\n                        }\n                    }\n                }).catch(error => {\n                    errorBucket.push(error.data.message);\n                     if(chunksTotals == (1+i)){\n                        this.saveContactListButtonDisable = false\n                        //here display all error responces from bucket\n                        let errorsText = errorBucket.filter(e=>!!e.length).join('<br>');\n                        Nova.error(errorsText);\n                        console.log('error:', errorBucket, ': At index:',i);\n                    }\n                })\n\n                // if(i == 0)\n                //     setTimeout(()=>{}, 1000);\n            });\n\n        } else {\n            Nova.warning('Empty or invalid or missing Entries')\n        }\n    },\n  },\n}\n</script>\n\n<style >\n.fieldmapper table{\n  width: 100%;\n  margin-top: 10px;\n}\n</style>\n"],"sourceRoot":""}]);
