mplate>\n    <template>\n      <Modal\n        :show=\"showChat\"\n        data-testid=\"conversation-modal\"\n        role=\"alertdialog\"\n      >\n        <div\n          class=\"bg-white dark:bg-gray-800 rounded-lg shadow-lg overflow-hidden\"\n          style=\"width: 900px\"\n        >\n          <ModalHeader v-text=\"__('Conversation Report')\" />\n          <ModalContent>\n            <div class=\"flex-container\">\n              <div class=\"item1\">\n                  <div v-for=\"chat in chatData\" :key=\"chat.time\" class=\"containerChat\" >\n                    <p>{{ chat.text }}</p>\n                    <span class=\"time-left\" >{{ chat.time }}</span>\n                  </div>\n\n                  <div  class=\"containerChat darker\">\n                    <p class=\"right\">{{ chatData[0].text }}</p>\n                    <br />\n                    <span class=\"time-right\">{{ chatData[0].time }}</span>\n                  </div>\n              </div>\n              <div class=\"item2\">\n                <div>\n                  <h1>Topics:</h1>\n                  <div style=\"width: 150px\">\n                    <span\n                    v-for=\"topic in topics\"\n                    :key=\"topic.id\"\n                      class=\"\n                        inline-flex\n                        items-center\n                        whitespace-nowrap\n                        h-6\n                        px-2\n                        m-2\n                        rounded-full\n                        uppercase\n                        text-xs\n                        font-bold\n                        badge-success\n                      \"\n                      >{{topic.title}}</span\n                    >\n                  </div>\n                </div>\n                <div>\n                  <h1 class=\"mt-4\">Words:</h1>\n                  <div style=\"width: 150px\">\n                    <span\n                      class=\"\n                        inline-flex\n                        items-center\n                        whitespace-nowrap\n                        h-6\n                        px-2\n                        m-2\n                        rounded-full\n                        uppercase\n                        text-xs\n                        font-bold\n                        badge-info\n                      \"\n                      >email</span\n                    >\n                  </div>\n                </div>\n              </div>\n            </div>\n            <div class=\"audio-player\">\n              <AudioPlayer\n                :option=\"{\n                  src: audioPath,\n                  title: filename,\n                  coverImage:\n                    'https://icon-library.com/images/headphones-png-icon/headphones-png-icon-11.jpg',\n                }\"\n              />\n            </div>\n          </ModalContent>\n          <ModalFooter>\n            <div class=\"ml-auto\">\n              <LinkButton\n                dusk=\"cancel-upload-delete-button\"\n                type=\"button\"\n                @click.prevent=\"showChat = false\"\n                class=\"mr-3\"\n              >\n                {{ __('Cancel') }}\n              </LinkButton>\n            </div>\n          </ModalFooter>\n        </div>\n      </Modal>\n    </template>\n    <!-- <template v-if=\"status == 'played' || status == 'pending'\"> -->\n  </div>\n</template>\n<script>\nimport AudioPlayer from 'vue3-audio-player'\nimport 'vue3-audio-player/dist/style.css'\n\nexport default {\n  props: {\n    resource: {\n      type: Object,\n      default: {},\n    },\n  },\n  components: {\n    AudioPlayer,\n  },\n  data() {\n    return {\n      showChat: false,\n      chatData: [],\n      topics: null,\n      audioPath: null\n    }\n  },\n  computed: {\n    status() {\n      return this.resource.fields.find(a => a.attribute == 'status').value\n    },\n    filename() {\n      return this.resource.fields.find(a => a.attribute == 'filename').value\n    },\n    id() {\n      return this.resource.fields.find(a => a.attribute == 'id').value\n    },\n  },\n  methods: {\n    async loadChat() {\n      console.log(\n        '?????????????????????????????????????????????????????????????????????????????'\n      )\n      console.log(this.filename)\n      await Nova.request()\n        .get('/nova-api/custom/audio/' + this.id)\n        .then(res => {\n          console.log(res.data)\n          this.chatData = res.data.chatData\n          this.topics = res.data.topics\n            // this.audioPath = URL.createObjectURL('https://rvm.nyc3.digitaloceanspaces.com/RVM/' + this.filename)\n            this.audioPath = 'https://rvm.nyc3.digitaloceanspaces.com/RVM/' + this.filename\n          console.log(this.topics)\n        })\n\n      this.showChat = true\n    },\n  },\n  mounted() {},\n}\n</script>\n\n<style scoped>\n.flex-container {\n  display: flex;\n\n  /* background-color: #bbdefb; */\n  height: 100%;\n  padding: 15px;\n  gap: 5px;\n}\n\n.flex-container > div {\n  /* background: #ffecb3; */\n  border: 3px solid rgba(var(--colors-primary-300));\n  border-radius: 5px;\n  padding: 8px;\n}\n\n.item1 {\n  /* flex:1 1 auto; */\n  flex-grow: 1;\n  flex-shrink: 1;\n  align-self: stretch;\n}\n\n.item2 {\n  /* flex:0 1 auto; */\n  order: 1;\n  flex-grow: 0;\n  flex-shrink: 1;\n  align-self: stretch;\n}\n.audio-player {\n  width: 100%;\n}\n\n/* chat messages */\n.containerChat {\n  border: 2px solid rgba(var(--colors-primary-300));\n  background-color: rgba(var(--colors-primary-100));\n  border-radius: 5px;\n  padding: 10px;\n  margin: 10px 0;\n  width: 50%;\n}\n\n.darker {\n  border-color: rgba(var(--colors-primary-100));\n  background-color: rgba(var(--colors-primary-300));\n  float: right;\n}\n\n.containerChat::after {\n  content: '';\n  clear: both;\n  display: table;\n}\n\n.containerChat img {\n  float: left;\n  max-width: 60px;\n  width: 100%;\n  margin-right: 20px;\n  border-radius: 50%;\n}\n\n.containerChat p.right {\n  float: right;\n  margin-left: 20px;\n  margin-right: 0;\n}\n\n.time-right {\n  float: right;\n  color: #aaa;\n}\n\n.time-left {\n  float: left;\n  color: #999;\n}\n</style>\n"],"sourceRoot":""}]);
